body {
    box-sizing: border-box;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 14px;
    margin: 0;
}

.navbar {
    padding: 15px;
    display: flex;
    justify-content: space-between;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
}

.dark-mode-toggle {
    font-weight: 600;
    font-size: 12px;
    
    display: flex;
    align-items: center;
    gap: 5px;
}

a {
    text-decoration: none;
    color: black;
}

.home-page {
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.home-page .search-bar {
    margin-bottom: 20px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
    border-radius: 10px;

    display: flex;
    gap: 20px;
    align-items: center;
}

.home-page input[name="search-input"] {
    border: none;
    outline: none;
}

.home-page input[name="search-input"]::placeholder {
    color: gray;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

.filter-by-region select {
    margin-bottom: 20px;
    padding: 20px;
    border: none;
    outline: none;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
    border-radius: 10px;
    /* Adds space right of arrow */
    border-right: 16px solid transparent;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

.country-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

.country-container {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
    border-radius: 8px;
    width: 80%;
}

.country-container img {
    width: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.country-text {
    padding: 20px;
    border-radius: 8px;
}

/* Detail page */

.country-detail {
    padding: 30px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.country-detail img {
    margin-top: 40px;
    margin-bottom: 40px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
}

.detail-page-text h1 {
    font-size: 20px;
    margin-bottom: 30px;
}

.back-button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 15px 0px;
    background-color: white;

    display: flex;
    align-items: center;
    gap: 8px;

    font-family: 'Nunito Sans', sans-serif;;
}

.border-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    flex-wrap: wrap;
}

.border-item {
    padding: 5px 10px;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 2px 10px 0px;
}